<template>
  <div class="category">
    <!-- 搜索框 -->
    <SearchInput></SearchInput>

    <div class="contain_wrap">
      <!-- 轮播图 开始-->
      <van-swipe indicator-color="#000" class="swiper_box" :autoplay="3000">
        <van-swipe-item class="swiper" v-for="item in detailData.pics" :key="item.pics_id">
          <van-image
            lazy-load
            :src="item.pics_mid_url?item.pics_mid_url:'https://ww1.sinaimg.cn/large/007rAy9hgy1g24by9t530j30i20i2glm.jpg'"
          />
        </van-swipe-item>
      </van-swipe>
      <!-- 轮播图 结束-->
      <!-- 价格收藏区 开始-->
      <div class="price_like_wrap">
        <div class="price">￥{{detailData.goods_price}}</div>
        <div class="text_like">
          <p class="text_like_left">{{detailData.goods_name}}</p>
          <div class="text_like_right" @click="shopLike">
            <i :class="['fa',isLike?'fa-heart':'fa-heart-o']"></i>
            <div class="collect_text">收藏</div>
          </div>
        </div>
      </div>
      <!-- 价格收藏区 结束-->
      <!-- 图文详情区 开始-->
      <div class="goods_info_wrap">
        <div class="goods_info_title">图片详情</div>
        <div v-html="detailData.goods_introduce"></div>
      </div>
      <!-- 图文详情区 结束---->
      <!-- 底部功能栏 开始-->
      <div class="btm_tool">
        <router-link class="tool_item navigator" to="/cart">
          <div class="fa fa-shopping-cart"></div>
          <div>购物车</div>
        </router-link>
        <div class="tool_item btn_cart" @click="handleCartAdd">
          <div>加入购物车</div>
        </div>
        <div class="tool_item btn_buy">
          <div>立即购买</div>
        </div>
      </div>
      <!-- 底部功能栏 结束-->
    </div>
  </div>
</template>

<script>
import { reaxios } from "@/assets/js/reaxios";
import "@/assets/css/category.css";
import SearchInput from "../components/SearchInput";
export default {
  data() {
    return {
      detailData: {},
      // 是否收藏商品
      isLike: false
    };
  },

  created() {
    const { goods_id } = this.$route.query;
    this.getData(goods_id);
  },
  methods: {
    /* 获取数据 */
    getData(id) {
      // 分类总数据

      reaxios(0, {
        url: "/goods/detail",
        params: {
          goods_id: Number(id)
        }
      }).then(result => {
        this.detailData = result.data.message;
        this.detailData.type = 1;
      });
    },
    // 商品收藏
    shopLike() {
      let collect = JSON.parse(localStorage.getItem("collect")) || [];

      // 判断是否有收藏 收藏为true 否为false goods_id
      let haveLike = collect.some(v => v.goods_id === this.detailData.goods_id);
      // 收藏商品
      if (!haveLike) {
        this.detailData.type = 1;
        collect.push(this.detailData);
        this.isLike = true;
      } else {
        let index = collect.findIndex(
          v => v.goods_id === this.detailData.goods_id
        );
        collect.splice(index, 1);
        this.isLike = false;
      }
      console.log(collect);

      localStorage.setItem("collect", JSON.stringify(collect));
    },
    // 加入购物车
    handleCartAdd() {
      // 购物车缓存数据
      let cart = JSON.parse(localStorage.getItem("cart")) || [];

      let index = cart.findIndex(v => v.goods_id === this.detailData.goods_id);

      if (index === -1) {
        // 不存在
        this.detailData.num = 1;
        this.detailData.check = true;
        cart.push(this.detailData);
      } else {
        cart[index].num++;
      }

      localStorage.setItem("cart", JSON.stringify(cart));
      this.$toast({
        message: "加入成功",
        icon: "like-o"
      });
    }
  },

  // 自定义组件注册
  components: { SearchInput }
};
</script>

<style lang="less" scoped>
.contain_wrap {
  padding: 50px 0 0;
  text-align: left;

  .swiper_box {
    display: flex;
    height: 80vw;
    .swiper {
      text-align: center;
      .van-image {
        width: 70%;
      }
    }
  }
  .fa {
    font-size: 18px;
  }
  .fa-heart {
    color: red;
  }

  .price_like_wrap {
    .price {
      color: var(--themeColor);
      font-size: 30px;
    }
    .text_like {
      display: flex;
      border-top: 2px solid rgb(233, 232, 232);
      border-bottom: 2px solid rgb(233, 232, 232);
      padding: 5px 0;
      .text_like_left {
        flex: 5;
        font-size: 15px;
        padding: 0 5px;

        display: -webkit-box;
        overflow: hidden;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
      }

      .text_like_right {
        flex: 1;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: center;
        align-items: center;
        border-left: 1px solid #ccc;
      }
    }
  }

  .goods_info_wrap {
    .goods_info_title {
      padding: 5px;
      font-size: 18px;
      color: var(--secondColor);
      margin-bottom: 5px;
      border-bottom: 5px solid rgb(233, 232, 232);
    }
  }

  .btm_tool {
    border-top: 2px solid rgb(233, 232, 232);
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 90rpx;
    background-color: #fff;
    display: flex;
    .tool_item {
      padding: 5px;
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      font-size: 12px;

      position: relative;

      color: #000;

      button {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
      }
    }
    .btn_cart {
      flex: 2;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-color: #ffc250;
      color: #fff;
      font-size: 15px;
      font-weight: 600;
    }
    .btn_buy {
      flex: 2;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      background-color: #f8542b;
      color: #fff;
      font-size: 15px;
      font-weight: 600;
    }
  }
}
</style>


